import { overlayAdd, overlayRemove } from '../overlay/overlay'

const modals = document.querySelectorAll('.modal')

export const openModal = (target) => {
	document.querySelector(`.modal[data-modal=${target}]`).classList.add('active')
	overlayAdd()
}

document.addEventListener('click', (e) => {
	const target = e.target
	if (!target.closest('.modal') && !target.closest('.recall') || target.closest('.modal__close')) {
		modals.forEach(item => item.classList.remove('active'))
		overlayRemove()
	}

	if (target.closest('.modal__submit')) {
		e.preventDefault()
		modals.forEach(item => item.classList.remove('active'))
		openModal('thanks')
	}
})

