import Swiper, { Pagination, Lazy } from 'swiper'
Swiper.use(Pagination, Lazy)

const investSlider = document.querySelector('.invest__slider')

const swiper = new Swiper(investSlider, {
	loop: true,
	lazy: true,
	pagination: {
		el: '.invest__pagination-years',
		clickable: true,
		renderBullet(index, className) {
			return `<div class="invest__pagination-year ${className}"><span>${2018 + index}</span></div>`
		}
	}
})
