const header = document.querySelector('.header')

const headerFixed = () => {
	if (self.scrollY > 0 && !header.classList.contains('fixed')) {
		header.classList.add('fixed')
	} else if (self.scrollY === 0) {
		header.classList.remove('fixed')
	}
}

headerFixed()

window.addEventListener('scroll', () => {
	headerFixed()
})
