import Swiper from 'swiper/bundle'

const slideImages = document.querySelectorAll('.service__image')
const slideTitles = document.querySelectorAll('.service__title')

// autoplay timer
const timer = 5000

const swiper1 = new Swiper('.service__container', {
	autoplay: {
		delay: timer,
		disableOnInteraction: false
	},
	spaceBetween: 30,
	centeredSlides: true,
	loop: true,
	navigation: {
		nextEl: '.service__next',
		prevEl: '.service__prev'
	},
	pagination: {
		el: '.service__pagination',
		clickable: true
	},
	slidesPerView: 5,
	breakpoints: {
		0: {
			slidesPerView: 2
		},
		768: {
			slidesPerView: 3
		},
		992: {
			slidesPerView: 4
		},
		1280: {
			slidesPerView: 5
		},
		1600: {
			centeredSlides: false
		}
	}
})

const swiper2 = new Swiper('.service__slider', {
	autoplay: {
		delay: timer,
		disableOnInteraction: false
	},
	spaceBetween: 30,
	loop: true,
	navigation: {
		nextEl: '.service__next',
		prevEl: '.service__prev'
	},
	thumbs: {
		swiper: swiper1
	},
	pagination: {
		el: '.service__pagination',
		clickable: true
	}
})

swiper2.on('slideChangeTransitionStart', () => {
	slideImages.forEach(item => item.classList.remove('active'))
	slideImages[swiper2.realIndex].classList.add('active')
})
