import { CountUp } from 'countup.js'

const numsPos = document.querySelector('.parametr').getBoundingClientRect().top

const counters = document.querySelectorAll('.parametr__value span[data-num]')

window.addEventListener('scroll', function onScroll() {
	if (window.pageYOffset > numsPos - window.innerHeight + 100) {
		counters.forEach(item => {
			const countUp = new CountUp(item, item.dataset.num)
			if (!countUp.error) {
				countUp.start()
			} else {
				console.error(countUp.error)
			}
		})
		window.removeEventListener('scroll', onScroll)
	}
})
